.footer{
    &__up::after {
        content: '';
        display: block;
        width: 60%;
        height: 1px;
        background-color: rgba(150, 150, 150, 1);
        margin-top: 30px;
        position: absolute;
    }
    a,p {
        color: white;
    }
    &__columns{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
        justify-items: center;
    }
    &__logo {
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        a {
            margin-left: 10px;
            font-family: 'Rubik', sans-serif;
            font-weight: 500;
        }
    }
    &__contact {
        display: flex;
        flex-direction: column;
    }
    &__social {
        margin-top: 10px;
    }
    &__down {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 35px;
        padding-bottom: 135px;
        &-right {
            a:not(:last-child) {
                margin-right: 50px;
            }
        }
    }

}

.col {
    display: flex;
    flex-direction: column;
    a:not(:last-child) {
        margin-bottom: 10px;
    }
    a:hover {
        color: #2f61eb;
      }


}